/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  /* box-shadow: inset 0 0 5px grey; */
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #00004d;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #00004d;
}
.main {
  background-image: url('images/bg.png');
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  background-size: cover;
}
.header-input {
  margin-top: 20px;
  float: right;
}
.input-box{
  width: 280px !important;
}
.Header {
  background-color: #3333ff;
  width: 100%;
  height: 15%;
  position: relative;
  border: 1px solid black;
}

.content {
  width: 100%;
  display: flex;
  align-items: flex-start;
  margin: 40px 0px 0px 90px;
}
.last-col {
  display: flex;
  margin-left: 3vw;
  flex-direction: column;
}
.red-btn {
  background-color: red;
  color: white;
  width: 11vw;
  height: 2.5vw;
  border-radius: 0px 0.8vw 0.8vw 0px;
  margin-bottom: 1vw;
  outline: none;
  border: none;
  font-size: 1.2vw;
  font-weight: bolder;
}
.black-btn {
  background-color: black;
  color: white;
  border-radius: 0px 0.8vw 0.8vw 0px;
  width: 11vw;
  outline: none;
  border: none;
  height: 2.5vw;
  font-size: 1.2vw;
  font-weight: bolder;
}
.key {
  float: right;
  padding: 30px 10px 10px 10px;
  cursor: pointer;
}
input[type='text'] {
  background-color: #000033;
  width: 20%;
  height: 33px;
  color: white;
  outline: none;
  background-position: 10px 10px;

  border-radius: 10px;
  padding-left: 20px;
}
.bottom {
  position: relative;
  width: 100%;
  height: 70%;
  margin-top: -50px;
  display: flex;
  margin-left: 5%;
  padding: 20px 10px 10px 10px;
  align-items: flex-start;
}

.left {
  border-radius: 20px 0px 0px 20px;
  color: white;
  position: relative;

  width: 25%;
  padding: 20px 10px 10px 20px;
  overflow-y: scroll;
  height: 70%;
  background-color: #3333ff;
}
.tbl {
  z-index: 0;
  position: relative;
  width: 60%;
  height: 70%;
  margin-left: 10%;
  overflow-y: scroll;
  border-radius: 20px 0px 0px 20px;
  color: white;
  padding: 20px 10px 10px 20px;
  background-color: #3333ff;
}
.right {
  float: right;
  z-index: 0;
  position: relative;
  width: 58%;
  height: 70%;
  margin-left: 7%;
  overflow-y: scroll;
  border-radius: 20px 0px 0px 20px;
  color: white;
  padding: 20px 10px 10px 20px;
  background-color: #3333ff;
}
.btn {
  background-color: #3333ff;
  border-radius: 8px;
  outline: none;
  color: white;

  width: 100px;
  border: none;
}
.buttons {
  width: 100%;
  height: 25px;
  display: flex;

  position: absolute;
  margin: -100px 0px 0px 130px;
}
.gallery {
  z-index: 1;
  border: 20px solid white;
  border-radius: 50%;
  display: 'inline-block';
  margin-left: 5%;
  margin-top: -70px;
  height: 168px;
  cursor: pointer;
}
.Wrap {
  display: inline-block;
}
.get {
  margin-top: -110px;
  margin-left: 55px;
}

.city {
  display: flex;
  justify-content: space-between;
}
.result_row {
  display: flex;
  align-items: flex-start;
}
.firstCol {
  width: 14%;
  vertical-align: top;
  height: inherit;
  display: inline-block;
}
.finalCol {
  width: 85.3%;
  height: inherit;
  vertical-align: top;
  display: inline-block;
}

.single_result {
  border-left: 1px solid white;
  width: 120px;
  text-align: center;
}
@media only screen and (max-width: 600px) {
  .gallery {
    width: 130px;
    height: 130px;
    margin-left: 12px;
    border: 5px solid white;
  }
  .last-col {
    margin-top: -3vw;
    margin-left: 2px;
  }
  .red-btn {
    font-size: 9px;
    width: 55px;
    border-radius: 0px 5px 5px 0px;

    height: 19px;
  }
  .black-btn {
    font-size: 9px;
    width: 55px;
    border-radius: 0px 5px 5px 0px;
    height: 19px;
  }
  .actionbtn {
    width: 30px;
    height: 30px;
    padding-top: 3px;
  }
  .finalCol {
    width: 83%;
  }
  .header-input {
    margin-left: 8px;
  }
  .content {
    margin-left: 8px;
  }
  .left {
    width: 32%;
    font-size: 8px;
    overflow-y: scroll;
  }
  .bottom {
    margin-left: -5px;
  }
  .btn {
    width: 50px;
  }
  .buttons {
    margin-left: 10px;
  }
  .key {
    padding-top: 20px;
  }
  .right {
    margin-left: 5px;
    padding-left: 1px;
    width: 90%;
    font-size: 6px;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  .image {
    width: 130px;
    height: 130px;
  }
  .get {
    margin-top: -100px;
    margin-left: 30px;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.image {
  margin-left: '40%';
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
